
import { defineComponent } from "vue";
import { XIcon } from "@heroicons/vue/outline";

export default defineComponent({
  name: "NavbarSearch",
  components: {
    XIcon,
  },
  emits: ["search"],
  data() {
    return {
      searchText: "",
    };
  },
  watch: {
    searchText() {
      const text = this.searchText.length >= 3 ? this.searchText : "";
      this.$emit("search", { text });
    },
  },
});
